/* Container Styling */
.loginContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Login Form Styling */
.loginForm {
  padding: 25px;
  margin: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 380px;
  box-sizing: border-box;
  text-align: center;
  border: 1px solid rgb(168, 174, 238);
}

.loginForm h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
  font-weight: 600;
}

/* Input Group Styling */
.inputGroup {
  margin-bottom: 20px;
}

.inputGroup label {
  font-size: 14px;
  color: #888;
  text-align: left;
  display: block;
  margin-bottom: 8px;
}

.inputGroup input {
  width: 90%;
  padding: 12px;
  font-size: 16px;
  color: #333;
  border: 1px solid #ddd;
  border-radius: 6px;
  outline: none;
  transition: border-color 0.3s ease;
}

.inputGroup input:focus {
  border-color: #007bff;
}

.inputGroup input::placeholder {
  color: #aaa;
}

/* Button Styling */
.loginBtn {
  width: 100%;
  padding: 12px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease-in-out;
}

.loginBtn:hover {
  background-color: #0056b3;
  transform: translateY(-2px); /* Button hover effect */
}

/* Error Message Styling */
.errorMessage {
  color: #e74c3c;
  font-size: 14px;
  margin-bottom: 15px;
}
