.user-info-container {
  max-width: 600px;
  margin: 40px auto;
  padding: 25px;
  border-radius: 16px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  font-family: "Arial", sans-serif;
  border: 1px solid rgb(168, 174, 238);
  background: linear-gradient(135deg, #ffffff, #f7f9fc);
  transform: scale(1.02);
  transition: 0.3s ease-in-out;
}

.user-info-title {
  font-size: 28px;
  color: #334155;
  text-align: center;
  position: relative;
}

.user-info-title::after {
  content: "";
  display: block;
  width: 50px;
  height: 3px;
  background-color: #6366f1;
  margin: 8px auto 0;
  border-radius: 2px;
}

.user-info-detail {
  background: rgba(99, 102, 241, 0.05);
  border-radius: 6px;
  padding: 10px;
  margin: 8px 0;
}

.user-info-detail span {
  color: #6366f1;
  font-weight: 600;
}

.user-info-detail:last-child {
  border-bottom: none;
}

.user-info-container p span {
  font-weight: bold;
  color: #222;
}

.signup-link {
  display: inline-block;
  padding: 6px 12px;
  background: #6366f1;
  color: #fff;
  border-radius: 6px;
  text-decoration: none;
  font-weight: 500;
  transition: 0.3s;
}

.signup-link:hover {
  background: #4f46e5;
}

/* Media Query for Mobile Responsiveness */
@media (max-width: 768px) {
  .user-info-container {
    max-width: 80%;
    padding: 15px;
  }

  .user-info-title {
    font-size: 24px;
    margin-bottom: 15px;
  }

  .user-info-detail {
    font-size: 16px;
    margin: 10px 0;
  }

  .user-info-container p span {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .user-info-title {
    font-size: 20px;
    margin-bottom: 12px;
  }

  .user-info-detail {
    font-size: 14px;
    margin: 8px 0;
  }

  .user-info-container p span {
    font-size: 12px;
  }

  .redirect-text {
    margin: 4px;
  }

  .signup-link {
    margin: 4px;
  }
}
