/* Container */
.homepageContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: "Poppins", sans-serif;
    background: #ffffff;
    text-align: center;
    padding: 20px;
    position: relative;
  }
  
  /* Profile Menu */
  .profileMenu {
    position: absolute;
    top: 20px;
    right: 20px;
  }
  
  /* Profile Button */
  .profileBtn {
    background: white;
    color: #007bff;
    border: 2px solid #007bff;
    padding: 12px;
    font-size: 20px;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }
  
  .profileBtn:hover {
    background: #007bff;
    color: white;
    transform: scale(1.1);
  }
  
  /* Dropdown Modal */
  .modal {
    position: absolute;
    top: 50px;
    right: 0;
    background: white;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    min-width: 150px;
    animation: fadeIn 0.3s ease-in-out;
  }
  
  /* Modal Items */
  .modalItem {
    padding: 12px;
    text-align: left;
    background: none;
    border: none;
    width: 100%;
    font-size: 16px;
    cursor: pointer;
    color: #333;
    text-decoration: none;
    display: block;
  }
  
  .modalItem:hover {
    background: #f1f1f1;
  }
  
  /* Fade-in animation */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-5px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  